/* shop by look section */

.shopByLook-section .owl-nav {
  display: block;
}

.shopByLook-section .owl-nav .owl-prev span,
.shopByLook-section .owl-nav .owl-next span {
  color: transparent;
}

.shopByLook-section .owl-theme .owl-nav [class*="owl-"]:hover {
  background: transparent;
  color: transparent;
  text-decoration: none;
  outline: 0;
}

.shopByLook-section .owl-nav .owl-prev span {
  position: absolute;
  display: block;
  width: 16px;
  height: 16px;
  margin: -8px 0 0 -8px;
  background: #ffffff;
  border-radius: 100%;
  box-shadow: 0 1px 10px rgb(0 0 0 / 25%);
  cursor: pointer;
  z-index: 1;
  transform: scale(1);
  transition: transform 0.25s ease-in-out;
  left: -120%;
  top: 76%;
}

.shopByLook-section .owl-nav .owl-prev::after {
  position: absolute;
  content: "";
  width: 37px;
  height: 38px;
  left: -12px;
  top: -12px;
  border-radius: 100%;
  background: rgba(255, 255, 255, 0.4);
  -webkit-animation: 1.4s shopTheLookDotKeyframe ease-in-out infinite;
  animation: 1.4s shopTheLookDotKeyframe ease-in-out infinite;
  left: -125%;
  top: 72%;
}

.shopByLook-section .owl-nav .owl-next span {
  position: absolute;
  display: block;
  width: 16px;
  height: 16px;
  margin: -8px 0 0 -8px;
  background: #ffffff;
  border-radius: 100%;
  box-shadow: 0 1px 10px rgb(0 0 0 / 25%);
  cursor: pointer;
  z-index: 1;
  transform: scale(1);
  transition: transform 0.25s ease-in-out;
  left: -72%;
  top: 52%;
}

.shopByLook-section .owl-nav .owl-next::after {
  position: absolute;
  content: "";
  width: 37px;
  height: 38px;
  left: -12px;
  top: -12px;
  border-radius: 100%;
  background: rgba(255, 255, 255, 0.4);
  -webkit-animation: 1.4s shopTheLookDotKeyframe ease-in-out infinite;
  animation: 1.4s shopTheLookDotKeyframe ease-in-out infinite;
  left: -77%;
  top: 48%;
}

